.ageType {
  color: #fff;
  display: inline-block;
  // padding: 5px;
  font-size: 14px;
  min-width: 33px;
  text-align: center;
  margin-right: 8px;
  border-radius: 4px;
  background-color: #00ac4d;
}

.tab {
  display: flex;
  justify-content: flex-start;
  height: 700px;
  ul li {
    list-style: none;
  }
  p,
  h4 {
    margin: 0;
    padding: 0;
  }
  .logoRap {
    border: 1px solid #ebebec;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    .btnLogo {
      background-color: #fff;
      border: none;
      cursor: pointer;
      padding: 20px 0;
      border-bottom: 1px solid #ebebec;
      transition: all 0.2s;
      &:hover {
        opacity: 1;
      }
    }
  }
  .diadiem__Rap {
    height: 100%;
    max-width: 100%;
    border-top: 1px solid #ebebec;
    border-bottom: 1px solid #ebebec;
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    padding: 0 10px;
    .rapInfo {
      cursor: pointer;
      max-width: 100%;
      display: flex;
      align-items: center;
      padding: 20px 0;
      border-bottom: 1px solid #ebebec;
      transition: all 0.2s;
      &:hover {
        opacity: 1;
        background-color: rgb(236, 232, 232);
      }
      .d-flex {
        padding-left: 15px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .infoMovieCinema {
          font-size: 12px;
          color: #949494;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          // margin: 5px 0;
        }
        a {
          color: #fb4226 !important;
          font-size: 12px;
        }
      }
    }
  }
  .listMovie {
    border: 1px solid #ebebec;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    overflow-x: hidden;
    overflow-y: auto;
    max-width: 100%;
    height: 700px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 10px;
    .wrapListMovie {
      width: 100%;
      padding: 20px 0;
      border-bottom: 1px solid #ebebec;
      .titleMovie {
        display: flex;
        align-items: center;
        
      }
      .lichCHieu {
        display: flex;
        overflow-x: hidden;
        overflow-y: auto;
        max-width: 100%;
        flex-wrap: wrap;
        align-items: flex-start;
      }
      .btnMovie {
        color: #000;
        border: 1px solid #e4e4e4;
        display: flex;
        padding: 5px;
        font-size: 14px;
        background: rgba(246, 246, 246, 0.5);
        align-items: center;
        font-family: "sfMedium";
        font-weight: 400;
        line-height: 1.42857143;
        border-radius: 7px;
        letter-spacing: normal;
        justify-content: center;
        text-decoration: none;
        margin: 3px;
        cursor: pointer;
        transition: all 0.2s;
        &:hover{
          background-color: rgb(221, 217, 217);
        }
      }
    }
  }
}
